/**
 * @function PURE
 * @description get the list of year
 * @param {number} maxYearsInFuture
 * @returns {array} Year list
 */
var getYears = function getYears() {
  var maxYearsInFuture = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : 20;
  var currentYear = Number(new Date().getFullYear().toString().substr(-2));
  var yearsToShow = currentYear + maxYearsInFuture - 1;
  var years = [];

  while (currentYear <= yearsToShow) {
    years.push("".concat(currentYear));
    currentYear++;
  }

  return years;
};

